          </div>
        </div>

        <div class="inputs">
          <div class="text-center" v-if="!isButton">
            <button class="btn btn-primary" @click="create()">Qo'shish</button>
          </div>
          <div class="text-center" v-if="isButton">
            <button class="btn btn-primary" @click="edit()">O'zgartirish</button>
          </div>
        </div>

      </div>
    </div>
    <!-- BootstrapVue Done -->
    <base-header class="pb-6 pb-8 pt-5 pt-md-8 ">
      <!-- Card stats -->
    </base-header>

    <b-container fluid class="mt--7">
      <form @submit.prevent="createOz()">
      <b-row class="justify-content-center">
        <b-col lg="8">
          <b-card class="p-5" no-body>
            <b-tabs>
            <b-tab no-body title="O'zbekcha">

                <div class="form_mains">
                  <div class="inputs">
                    <div>
                      <label>Title</label>
                    </div>
                    <div>
                      <input type="text" v-model="title" class="form-control">
                    </div>
                  </div>



                </div>
            </b-tab>

            <b-tab no-body title="Rus">
              <div class="inputs">
                <div>
                  <label>Title</label>
                </div>
