<script>
import projects from '../../Tables/projects'
import {Table, TableColumn} from 'element-ui'
import Users from '../../Tables/users'
import axios from "axios";
import {baseUrl} from "../../../config";

export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      menus:[],
      rows: 1,
      perPage: 1,
      currentPage: 1,
      index: 0,
      right_t: false,
      data_main: [],
      title:'',
      titleru:'',
      titleen:'',
      order:'',
      isFooter:false,
      url:'',
      isButton:true,
      main_id:''
    };
  },
  methods: {
    rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    createOz(){
      axios.post(`${baseUrl}menus/`,{
          "title":this.title,
          "order":this.order,
          "url":this.url,
          "is_footer":this.isFooter
        },
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'oz'
          }
        })
        .then(res=>{
              this.isFooter=false
              this.url=''
              this.order=''
              this.title=''
          this.$toast("Ma'lumotlar  o'zbek tilida kiritildi", {
            timeout: 2000,
            type:"success"
          });
          //ru
          axios.patch(`${baseUrl}menus/${res.data.id}/`,{
              "title":this.titleru
            },
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Accept-Language':'ru'
              }
            })
            .then(()=>{
              this.isFooter=false
              this.url=''
              this.order=''
              this.title=''

              this.$toast("Ma'lumotlar rus va o'zbek tillarda kiritildi", {
                timeout: 2000,
                type:"success"
              });
              //en
              axios.patch(`${baseUrl}menus/${res.data.id}/`,{
                  "title":this.titleen
                },
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':'kar'
                  }
                })
                .then(()=>{
                  this.isFooter=false
                  this.url=''
                  this.order=''
                  this.title=''

                  this.$toast("Ma'lumotlar barcha tillarda kiritildi", {
                    timeout: 2000,
                    type:"success"
                  });
                })
              //

            })
          //

        })
      .catch(()=>{
        this.$toast("Xatolik bor", {
          timeout: 2000,
          type:"error"
        });
      })
    },
    edit(){
      axios.patch(`${baseUrl}menus/${this.main_id}/`,{
          "title":this.title,
          "order":this.order,
          "url":this.url,
          "is_footer":this.isFooter
        },
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        })
        .then(()=>{
          axios.get(`${baseUrl}menus/`)
            .then(res=>{
              this.menus=res.data
              this.isFooter=false
              this.url=''
              this.order=''
              this.title=''
              this.right_t =false
            })
        })
    },
    edit_main(row){
      this.main_id=row.id
      this.isButton=true
      this.isFooter=row.is_footer
      this.url=row.url
      this.order=row.order
      this.title=row.title
    }
  },
  mounted() {
    axios.get(`${baseUrl}menus/`)
      .then(res=>{
        this.menus=res.data
        console.log(res)
      })
  }
}

</script>
